<style scoped lang="scss">
.banner {
  width: calc(100% - 500px);
  height: 100vh;
  background-image: linear-gradient(120deg, #e0c3fc 0%, #8ec5fc 100%);
  float: left;
}
.content {
  width: 500px;
  height: 100vh;
  background-color: #fff;
  float: left;
  text-align: center;
  position: relative;
  .menu {
    margin-top: 100px;
    margin-bottom: 50px;
  }
  .signIn {
    width: 300px;
    display: block;
    margin: 0 auto;
    .el-form-item {
      margin-top: 60px;
    }
    .visitorBtn {
      position: absolute;
      left: 320px;
      top: 410px;
    }
  }
  .signUp {
    width: 300px;
    display: block;
    margin: -15px auto;
    .el-form-item {
      margin-top: 20px;
    }
    .vInput {
      width: 80px;
      float: left;
    }
    .sendEmailBtn {
      width: 120px;
      float: right;
    }
  }
  .submitBtn {
    width: 200px;
    margin: 0;
  }
}
</style>

<template>
  <div>
    <div class="banner"></div>
    <div class="content">
      <el-radio-group class="menu" v-model="menu" :change="changeMenu()">
        <el-radio-button label="signIn"></el-radio-button>
        <el-radio-button label="signUp"></el-radio-button>
      </el-radio-group>
      <el-form
        v-show="menu === 'signIn'"
        class="signIn"
        ref="signIn"
        :model="signIn"
        label-width="65px"
        label-position="left"
        :rules='signInRules'
      >
        <el-form-item label="" prop="name">
          <el-input v-model="signIn.name"></el-input>
        </el-form-item>
        <el-form-item label="" prop="pass">
          <el-input type="password" v-model="signIn.pass"></el-input>
        </el-form-item>
        <el-button class="visitorBtn" type="text" @click="visitor()"></el-button>
        <el-form-item>
          <el-button class="submitBtn" type="primary" @click="onSubmitIn('signIn')"></el-button>
        </el-form-item>
      </el-form>
      <el-form
        v-show="menu === 'signUp'"
        class="signUp"
        ref="signUp"
        :model="signUp"
        label-width="80px"
        label-position="left"
        :rules="signUpRules"
      >
        <el-form-item label="" prop="name">
          <el-input v-model="signUp.name"></el-input>
        </el-form-item>
        <el-form-item label="" prop="pass">
          <el-input type="password" v-model="signUp.pass"></el-input>
        </el-form-item>
        <el-form-item label="" prop="checkPass">
          <el-input type="password" v-model="signUp.checkPass"></el-input>
        </el-form-item>
        <el-form-item label="" prop="sid">
          <el-input v-model="signUp.sid"></el-input>
        </el-form-item>
        <el-form-item label="" prop="sPass">
          <el-input type="password" v-model="signUp.sPass"></el-input>
        </el-form-item>
        <el-form-item label="" prop="email">
          <el-input v-model="signUp.email"></el-input>
        </el-form-item>
        <el-form-item label="" prop="verification">
          <el-input v-model="signUp.verification" class="vInput"></el-input>
          <el-button
            class="sendEmailBtn"
            @click="sendEmail()"
            :disabled="sendEmailBtnDisabled"
          >{{sendEmailBtnText}}</el-button>
        </el-form-item>
        <el-form-item>
          <el-button class="submitBtn" type="primary" @click="onSubmitUp('signUp')"></el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>
<script>
import Sycn from "./../js/util/sync.js";
export default {
  data() {
    // 
    const validatePass = (rule, value, callback) => {
      if (value === "") {
        callback(new Error(""));
      } else if (value.length < 6 || value.length > 15) {
        callback(new Error(" 6  15 "));
      } else {
        if (this.signUp.checkPass !== "") {
          this.$refs.signUp.validateField("checkPass");
        }
        callback();
      }
    };
    const validatePass2 = (rule, value, callback) => {
      if (value === "") {
        callback(new Error(""));
      } else if (value !== this.signUp.pass) {
        callback(new Error("!"));
      } else {
        callback();
      }
    };
    return {
      menu: "signIn",
      signIn: {
        name: "",
        pass: "",
      },
      signInRules: {
        name: [
          { required: true, message: "", trigger: "blur" },
          {
            min: 3,
            max: 15,
            message: " 3  15 ",
            trigger: "blur"
          }
        ],
        pass: [
          { required: true, message: "", trigger: "blur" },
          {
            min: 3,
            max: 15,
            message: " 6  15 ",
            trigger: "blur"
          }
        ]
      },
      signUp: {
        name: "",
        pass: "",
        checkPass: "",
        sid: "",
        sPass: "",
        email: "",
        verification: ""
      },
      signUpRules: {
        name: [
          { required: true, message: "", trigger: "change" },
          {
            min: 3,
            max: 15,
            message: " 3  15 ",
            trigger: "change"
          }
        ],
        pass: [
          { required: true, message: "", trigger: "change" },
          { validator: validatePass, trigger: "blur" }
        ],
        checkPass: [
          { required: true, message: "", trigger: "change" },
          { validator: validatePass2, trigger: "blur" }
        ],
        sid: [
          { required: true, message: "", trigger: "change" },
          { min: 10, max: 15, message: "", trigger: "change" }
        ],
        sPass: [
          { required: true, message: "", trigger: "change" }
        ],
        email: [{ required: true, message: "", trigger: "change" }],
        verification: [
          { required: true, message: "", trigger: "change" }
        ]
      },
      sendEmailStatus: 0,
      sendEmailBtnText: "",
      sendEmailBtnDisabled: true,
      sendEmailInterval: null,
      sendEmailTime: 0
    };
  },
  mounted() {
    this.sendEmailBtnText = "";
    this.sendEmailStatus = 0;
    this.sendEmailBtnDisabled = true;
  },
  watch: {
    "signUp.email"(val, oldVal) {
      // 
      if (val != "" && new Date().getTime() - this.sendEmailTime > 60000) {
        this.sendEmailBtnDisabled = false;
      } else {
        this.sendEmailBtnDisabled = true;
      }
    }
  },
  methods: {
    // tab
    changeMenu() {},
    // 
    onSubmitIn(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          const sync = new Sycn();
          sync
            .POST("/api/login", this.signIn)
            .then(data => {
              if (data.errno === 0) {
                localStorage.setItem('userInfo', JSON.stringify(data.data));
                this.$message({
                  showClose: true,
                  message: '',
                  type: 'success'
                });
                this.$router.push("home");
              } else {
                this.$message({
                  showClose: true,
                  message: data.errmsg,
                  type: 'error'
                });
              }
            })
            .catch(err => {
                this.$message({
                  showClose: true,
                  message: ',,!',
                  type: 'error'
                });
            });
        } else {
          this.$message({
            showClose: true,
            message: ',!',
            type: 'error'
          });
          return false;
        }
      });
    },
    // 
    onSubmitUp(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          const sync = new Sycn();
          sync
            .POST("/api/register", this.signUp)
            .then(data => {
              if (data.errno === 0) {
                localStorage.setItem('userInfo', JSON.stringify(data.data));
                this.$message({
                  showClose: true,
                  message: '',
                  type: 'success'
                });
                this.$router.push("home");
              } else {
                this.$message({
                  showClose: true,
                  message: data.errmsg,
                  type: 'error'
                });
              }
            })
            .catch(err => {
                this.$message({
                  showClose: true,
                  message: ',,!',
                  type: 'error'
                });
            });
        } else {
          this.$message({
            showClose: true,
            message: ',!',
            type: 'error'
          });
          return false;
        }
      });
    },
    // 
    visitor() {
      this.$router.push("home");
    },
    // 
    sendEmail() {
      // 
      if (this.sendEmailStatus === 0 || this.sendEmailStatus === 2) {
        const sync = new Sycn();
        if (this.url != "") {
          sync
            .POST("/api/sendMail", {
              html: `<h3>ccsu-bbs<VerificationCode></h3>`,
              title: "",
              emailTo: this.signUp.email,
              type: 1
            })
            .then(data => {
              console.log(data);
            })
            .catch(err => {
              console.log(err);
            });
        } else {
          console.log("no img");
        }
        this.sendEmailStatus = 1;
        this.sendEmailBtnDisabled = true;
        this.sendEmailTime = new Date().getTime();
        // ,2
        this.sendEmailInterval = setInterval(() => {
          const hadTime =
            60 - (new Date().getTime() - this.sendEmailTime) / 1000;
          this.sendEmailBtnText = `(${parseInt(hadTime)})`;
          if (hadTime < 0) {
            clearInterval(this.sendEmailInterval);
            this.sendEmailStatus = 2;
            this.sendEmailBtnText = ``;
            // 
            if (this.signUp.email != "") {
              this.sendEmailBtnDisabled = false;
            }
          }
        }, 100);
        // 
      }
    }
  }
};
</script>
