<style scoped lang="scss">

</style>
<template>
  <el-table
    :data="userInfos"
    stripe
    style="width: 100%">
    <el-table-column
      prop="name"
      label="">
    </el-table-column>
    <el-table-column
      prop="sex"
      label=""
      width="50">
      <template slot-scope="scope">
        <span v-if="scope.row.sex === 1" ></span>
        <span v-if="scope.row.sex === 2" ></span>
      </template>
    </el-table-column>
    <el-table-column
      prop="sid"
      label=""
      width="120">
    </el-table-column>
    <el-table-column
      prop="email"
      label="">
    </el-table-column>
    <el-table-column
      prop="phone"
      label=""
      width="110">
    </el-table-column>
    <el-table-column
      prop="birthday"
      label=""
      width="100">
    </el-table-column>
    <el-table-column
      prop="last_login_at"
      label=""
      width="160">
    </el-table-column>
    <el-table-column
      prop="create_at"
      label=""
      width="160">
    </el-table-column>
    <el-table-column
      prop="operation"
      label="">
      <template slot-scope="scope">
        <el-button v-if="scope.row.status === 1" @click="blackHoust(scope.row.id, 2)" type="text" size="small"></el-button>
        <el-button v-else @click="blackHoust(scope.row.id, 1)" type="text" size="small"></el-button>
      </template>
    </el-table-column>
  </el-table>
</template>

<script>
  import Sycn from "./../../js/util/sync.js";
  export default {
    data() {
      return {
        userInfos: []
      }
    },
    mounted() {
      this.getData();
    },
    methods: {
         blackHoust(id, status) {
          const sync = new Sycn();
          sync.GET("/api/admin/user/put", {id, status})
              .then(data => {
                this.$message({
                  showClose: true,
                  message: '',
                  type: 'success'
                });
                this.getData();
              });
         },
         getData() {
          const sync = new Sycn();
          sync.GET("/api/admin/user/get")
              .then(data => {
                this.userInfos = data.data;
              });
         }
    },
  }
</script>