<style scoped lang="scss">
</style>
<template>
  <el-table
    :data="list"
    stripe
    style="width: 100%">
    <el-table-column
      prop="title"
      label="">
    </el-table-column>
    <el-table-column
      prop="landlord"
      label="">
    </el-table-column>
    <el-table-column
      prop="anonymous"
      label=""
      width="80">
      <template slot-scope="scope">
        <span v-if="scope.row.anonymous === 0" ></span>
        <span v-if="scope.row.anonymous === 1" ></span>
      </template>
    </el-table-column>
    <el-table-column
      prop="status"
      label=""
      width="100">
      <template slot-scope="scope">
        <span v-if="scope.row.status === 1" ></span>
        <span v-if="scope.row.status === 2" ></span>
        <span v-if="scope.row.status === 3" ></span>
        <span v-if="scope.row.status === 4" ></span>
      </template>
    </el-table-column>
    <el-table-column
      prop="count"
      label=""
      width="80">
    </el-table-column>
    <el-table-column
      prop="create_at"
      label=""
      width="160">
    </el-table-column>
    <el-table-column
      prop="operation"
      label="">
      <template slot-scope="scope">
        <el-button v-if="scope.row.status !== 4" @click="operation(scope.row.id, 4)" type="text" size="small"></el-button>
        <el-button v-if="scope.row.status === 2 || scope.row.status === 3 ||scope.row.status === 4" @click="operation(scope.row.id, 1)" type="text" size="small"></el-button>
        <el-button v-if="scope.row.status === 1" @click="operation(scope.row.id, 2)" type="text" size="small"></el-button>
        <el-button v-if="scope.row.status === 1" @click="operation(scope.row.id, 3)" type="text" size="small"></el-button>
      </template>
    </el-table-column>
  </el-table>
</template>

<script>
  import Sycn from "./../../js/util/sync.js";
  export default {
    data() {
      return {
        list: []
      }
    },
    mounted() {
      this.getData();
    },
    watch:{
      $route(to,from){
        this.getData();
      }
    },
    methods: {
      getData() {
        const url = this.$route.params.label_url;
        const sync = new Sycn();
        sync.GET(`/api/admin/list/${url}/get`)
          .then(data => {
            this.list = data.data;
          });
      },
      operation(id, status) {
        const url = this.$route.params.label_url;
        const sync = new Sycn();
        sync.POST(`/api/admin/list/${url}/put`, {id, status})
          .then(data => {
            if (data.errno === 0) {
              this.$message({
                showClose: true,
                message: '',
                type: 'success'
              });
              this.getData();
            } else {
              this.$message({
                showClose: true,
                message: data.errmsg,
                type: 'error'
              });
            }
          });
      }
    },
  }
</script>